<template>
    <div class="container">
        <link href="https://fonts.googleapis.com/icon?family=Material+Icons"
              rel="stylesheet">
        <div class="signin-wrapper" v-if="!userCodeOK && !isAuthenticated()">
            <button v-on:click="authorizeHandler()">Sign In</button>
        </div>
        <div class="user-code" v-if="userCodeOK && !isAuthenticated()">
            <h3>Please visit {{ url }} to connect your mirror :</h3>
            <h2>{{ userCode }}</h2>
        </div>
        <div class="facebook-container">
            <div class="container_posts">
                <div class="single_post" v-for="(post, index) in posts" :key="index">
                    <div class="profile">
                        <img :src="user_picture"/>
                        <span class="user_name">{{user_name}}</span>
                    </div>
                    <div class="checkMessage" v-if="post.message">
                        <span class="title">{{post.message}}</span>
                    </div>
                    <div class="checkAttachments" v-if="post.attachments">
                        <div v-for="(image, index) in post.attachments.data" :key="index">
                            <span v-if="image.media">
                                <img class="media" :src="image.media.image.src">
                            </span>
                        </div>
                    </div>
                    <div class="stats">
                        <span class="nb_comments">
                            {{post.comments.summary.total_count}}
                            <img class="nb_comments_icon" src="../assets/fb_comments.png"/>
                        </span>

                        <span class="nb_likes">{{post.likes.summary.total_count}}</span>
                        <button class="button_like"
                                v-on:click="!post.hasLiked ? (post.likes.summary.total_count++, post.hasLiked = true) : (post.likes.summary.total_count--, post.hasLiked = false)">
                            <img class="nb_likes_icon" v-if="!post.hasLiked" src="../assets/like_button_white.png"/>
                            <img class="nb_likes_icon" v-else src="../assets/like_button_blue.png"/>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'facebook',
    data() {
      return {
        message: '',
        userToken: '',
        userPicture: '',
        clientToken: '76d37553d45bc0cf6565b5a4cfab6d3a',
        accessToken: '',
        userCodeOK: '',
        userCode: '',
        deviceCode: '',
        expiresIn: '',
        interval: '',
        url: '',
        posts: '',
        nbLikes: 0,
        hasLiked: false,
        user_name: '',
        user_id: '',
        user_picture: '',
        appId: '273589210137157'
      }
    },
    mounted: function () {
      /* eslint-disable */
      window.fbAsyncInit = function () {
        /* global FB */
        FB.init({
          appId: this.appId,
          autoLogAppEvents: true,
          xfbml: true,
          version: 'v3.1'
        });
      };
      (function (d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) {
          return;
        }
        js = d.createElement(s);
        js.id = id;
        js.src = "https://connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
      }(document, 'script', 'facebook-jssdk'));
      // this.accessToken = 'EAAD40ZCOvQkUBAPY1wZBFb1jWlinByyV4M5zVfEBEarepMlbZCvNUdF5l8u9kvwM5TGQ5gzhJYpjWy5N5d79jwpDrTvUCo9uXqjlDp0eXt2DHvjheej9izJ0vDqQNugp6WaakrlZCA3sD4ZAKZAyquygVaXdAWj1jKZB34r3PNTZCgZDZD'
      this.displayFeed()
      this.getUserId()
    },
    methods: {

      authorizeHandler: function () {
        console.log(this.appId)
        console.log(this.accessToken)
        axios.post(`https://graph.facebook.com/v3.1/device/login`, {access_token: this.appId + '|' + this.clientToken}).then(response => {
          this.userCodeOK = true
          this.userCode = response.data['user_code']
          console.log(this.userToken)
          console.log(this.userCode)
          console.log(response)
          this.url = response.data['verification_uri']
          this.deviceCode = response.data['code']
          this.poll(() => {
            return axios.post(`https://graph.facebook.com/v3.1/device/login_status`, {
              access_token: this.appId + '|' + this.clientToken,
              code: this.deviceCode
            })
          }, response.data['expires_in'] * 1000, response.data['interval'] * 1000).then(response => {
            this.expiresIn = response.data['expires_in']
            this.interval = response.data['interval']
            this.accessToken = response.data['access_token']
            this.displayFeed()
            this.getUserId()
            this.userCodeOK = false
          }).catch(err => {
            if (err['timedOut']) {
              this.authorizeHandler()
            }
          })
        }, error => {
          console.error('ERROR UREFLECT : ' + error)
        })
      },
      poll: function (fn, timeout, interval) {
        const endTime = Number(new Date()) + timeout
        const query = ((resolve, reject) => {
          fn().then(response => {
            resolve(response)
          }).catch(() => {
            if (Number(new Date()) < endTime) {
              setTimeout(query, interval, resolve, reject)
            } else {
              reject({timedOut: true})
            }
          })
        })
        this.displayFeed()
        return new Promise(query)
      },
      isAuthenticated: function () {
        if (this.accessToken === null)
          return false
        else if (this.accessToken.length > 0)
          return true
        return false
      },
      displayFeed: function () {
        axios.get('https://graph.facebook.com/v3.1/me?fields=name,feed.limit(10){attachments,message,story,created_time,likes.limit(1).summary(true),comments.limit(1).summary(true)}&access_token=' + this.accessToken).then(response => {
          console.log('RESPONSE BB', response)
          this.user_name = response.data.name
          this.posts = response.data.feed.data
          console.log(this.posts)
        }, error => {
          console.error('ERROR UREFLECT : ' + error)
        })
      },
      getUserId: function () {
        axios.get('https://graph.facebook.com/me?access_token=' + this.accessToken).then(response => {
          console.log('ID BOBBY', response)
          this.user_id = response.data.id
          this.getUserPicture()
        }, error => {
          console.error('ERROR UREFLECT : ' + error)
        })
      },
      getUserPicture: function () {
        axios.get('https://graph.facebook.com/' + this.user_id + '/picture?fields=url&redirect=false').then(response => {
          this.user_picture = response.data['data'].url
          console.log('Picture: ', this.user_picture)

        }, error => {
          console.error('ERROR UREFLECT : ' + error)
        })
      },
      didLikePost: function (nbOfLikes, isClicked) {
        console.log("NB OF LIKE: ", nbOfLikes)
        console.log("IS CLICKED: ", isClicked)
        this.hasLiked = isClicked
        this.nbLikes = nbOfLikes
        console.log("NB OF LIKE: ", this.hasLiked)
        console.log("IS CLICKED: ", this.nbLikes)
        if (this.hasLiked == false) {
          this.nbLikes = this.nbLikes + 1
          this.hasLiked = true
        } else {
          this.nbLikes = this.nbLikes - 1
          this.hasLiked = false
        }
      }
      // didDislikePost: function (postId) {
      //   axios.delete('https://graph.facebook.com/v3.1/' + postId + '/likes&access_token=' + this.accessToken).then(response => {
      //
      //   }, error => {
      //     console.error('ERROR UREFLECT : ' + error)
      //   })
      // }
    }
  }

</script>

